import './style.css'
import '@webswing-migration-frontend/webswing-webcomponents'
import { IInjectorApi, IWebswingInstance } from 'webswing-api/dist/types';
import { getWebswingApi } from 'webswing-api';
import { getParam } from './utils';
import { WsModal, WsToast, WsTabs, WsTab, WsMap, WsDetailForm, WsIntro } from '@webswing-migration-frontend/webswing-webcomponents';
import { ITableData, WsTable } from '@webswing-migration-frontend/webswing-webcomponents/dist/ws-table/ws-table';
import { IFormData } from '@webswing-migration-frontend/webswing-webcomponents/dist/ws-detail-form/ws-detail-form';

class MigrationApp {
	parent: HTMLElement;
	args: string
	connectionUrl: string

	elementSelectedTab: WsTab;
	elementTable: WsTable;
	elementMap: WsMap;
	elementIntro: WsIntro;
	elementToast: WsToast;
	elementTabs: WsTabs;
	elementCreateForm: WsDetailForm;
	elementEditForm: WsDetailForm;
	elementModalEditItem: WsModal;
	elementModalCreateItem: WsModal;
	elementModalDeleteItem: WsModal;
	elementWebswingRoot: HTMLElement;
	elementCreateRecord: HTMLElement;
	
	private _webswingInstance: IWebswingInstance | undefined;
	
	public get webswingInstance(): IWebswingInstance | undefined {
		return this._webswingInstance;
	}
	public set webswingInstance(value: IWebswingInstance | undefined) {
		this._webswingInstance = value;
	}

	public getArrayOfTableCols = (col: keyof ITableData): string[] => {
		const r = (this.elementTable as WsTable ).data.map((row: ITableData) => row[col])
		return Array.from(new Set(r));
	}

	getServerUrl = () => {
		const href = window.location.href;
		return href.substring(0, href.indexOf('/migration-examples')) + '/migration-examples';
	}

	constructor(el: HTMLElement) {
		this.parent = el;
		this.args = '';
		this.connectionUrl = this.getServerUrl();

		this.elementTable = el.querySelector('ws-table') as WsTable,
		this.elementMap = el.querySelector('ws-map') as WsMap,
		this.elementSelectedTab = el.querySelector('ws-tab[selected="true"]') as WsTab,
		this.elementIntro = el.querySelector('ws-intro') as WsIntro,
		this.elementToast = el.querySelector('ws-toast') as WsToast,
		this.elementTabs = el.querySelector('ws-tabs') as WsTabs,
		this.elementCreateForm = el.querySelector('ws-detail-form[data-id="createForm"]') as WsDetailForm,
		this.elementEditForm = el.querySelector('ws-detail-form[data-id="editForm"]') as WsDetailForm,
		this.elementModalEditItem = el.querySelector('ws-modal[data-id="editRecord"]') as WsModal,
		this.elementModalCreateItem = el.querySelector('ws-modal[data-id="createRecord"]') as WsModal,
		this.elementWebswingRoot = el.querySelector('[data-id="webswing-table"]') as HTMLElement,
		this.elementModalDeleteItem = el.querySelector('ws-modal[data-id="deleteRecord"]') as WsModal,
		this.elementCreateRecord = el.querySelector('button[data-id="createRecord"]') as HTMLElement,

		this.webswingInit()
		this.uiIntro()
	}

	webswingInit = () => {
		getWebswingApi(this.connectionUrl)
		.then((api) => {
			this.webswingInstance = api.bootstrap(
			this.elementWebswingRoot,
			{
				connectionUrl: this.connectionUrl,
				autoStart: false,
				args: this.args,
				debugPort: Number(getParam('debugPort')),
			  	onReady: () => {
					this.elementWebswingRoot.classList.add('authenticated')
					this.webswingInstance?.start()
				},
			},
			(injector: IInjectorApi) => {
				// disable welcome back bar
				injector.services.dialog!.content.continueOldSessionDialog = null;

				injector.services.base!.handleActionEvent= (actionName: string, data: any) => {
					if (actionName==='initialize'){
						this.webswingInstance?.performAction({actionName:"switchStage",data:"4"})
						this.webswingInstance?.performAction({actionName:"getTableData"})
					}
					if (actionName==='setTableData'){
						const tableData = JSON.parse(data)
						this.uiTableInit(tableData)
					}
					if (actionName==='clickedAddButton') {
						const modal = this.elementModalCreateItem;
						const form = this.elementCreateForm;
						
						modal.header = 'Create Nobel prize winner record';
						modal.show = true;
						
						if(data) {
							const {categoryList, countryList} = JSON.parse(data);
							form.categories = categoryList;
							form.countries = countryList;
						}
						
						modal.addEventListener('close', (result: CustomEventInit<string>) => {
							if(result.detail === 'ok') {
								const data = JSON.stringify(form.data)
								this.webswingInstance?.performAction({actionName:"createItem",data})
								form.reset()
							}
						});
					}
					if (actionName==='editWinner') {
						const modal = this.elementModalEditItem;
						const form = this.elementEditForm;

						modal.header = 'Edit Nobel prize winner record';
						modal.show = true;

						if(data) {
							const {categoryList, countryList, nobelPrizeWinner, row} = JSON.parse(data);
							form.categories = categoryList;
							form.countries = countryList;
							form.data = nobelPrizeWinner;
							modal.addEventListener('close', (result: CustomEventInit<string>) => {
								if(result.detail === 'ok') {
									(form.data as any)['row'] = row
									const data = JSON.stringify(form.data)
									this.webswingInstance?.performAction({actionName:"editItem",data})
								}
							});
						}
					}
				}
			}
		  )
		})
		.catch((e) => {
		  console.error(e)
		})
	}

	uiTableInit = (data: ITableData[]) => {
		const toast = this.elementToast;
		const table = this.elementTable;
		const modal = this.elementModalCreateItem;
		const form = this.elementCreateForm;
		const map = this.elementMap;

		table.headers = ["ID", "Name", "Category", "Award Year", "Country"]

		table.data = data

		const modalCloseListener = (result: CustomEventInit<string>) => {
			modal.removeEventListener('close', modalCloseListener);
			if(result.detail === 'ok') {
				table.createRow(form.data)
				this.webswingInstance?.performAction({actionName:"createItem", data: JSON.stringify(form.data)})
				toast.show('Record has been created sucessfuly')
			}
		}

		table.shadowRoot?.querySelector("button[data-id='createRecord']")?.addEventListener('click', () => {
			modal.header = 'Create Nobel prize winner record';
			form.categories = this.getArrayOfTableCols('category');
			form.countries = this.getArrayOfTableCols('country');
			modal.show = true;
		});


		map.addEventListener('click', (e: any) => {
			const country = (e.composedPath()[0] as HTMLElement).getAttribute('data-name');
			if (country) {
				table.search = country
			}
		});

		modal.addEventListener('close', modalCloseListener);
		
		table.addEventListener('viewRow', (e: CustomEventInit)=> {
			this.createDetailClosableTab(e.detail)
		});

		table.addEventListener('editRow', (e: CustomEventInit)=> {
			this.editSingleTableRow(e.detail)
		});

		table.addEventListener('deleteRow', (e: CustomEventInit)=> {
			this.deleteSingleTableRow(e.detail)
		});

		table.addEventListener('selectRow', (e: CustomEventInit)=> {
			if(map.active === e.detail) {
				map.active = ''
				return
			}
			map.active = e.detail
		});

	}

	createDetailClosableTab(data: any) {
		const tab = document.createElement('ws-tab') as WsTab; 
		const wsDetailForm = document.createElement('ws-detail-form') as WsDetailForm;
		tab.label = data.name;
		this.elementTabs.appendChild(tab);
		tab.appendChild(wsDetailForm);
		this.elementTabs.selectTab(tab)
		tab.closable = true;

		wsDetailForm.data = data;
		wsDetailForm.readonly = true;
	}

	editSingleTableRow(data: IFormData) {
		if(!data) return

		const table = this.elementTable;
		const modal = this.elementModalEditItem;
		const form = this.elementEditForm;
		const toast = this.elementToast;

		modal.header = 'Edit Nobel prize winner record';
		modal.show = true;

		form.categories = this.getArrayOfTableCols('category');
		form.countries = this.getArrayOfTableCols('country');
		form.data = data as IFormData;

		const modalCloseListener = (result: CustomEventInit<string>) => {
			modal.removeEventListener('close', modalCloseListener)
			if(result.detail === 'ok') {
				// @ts-ignore
				const index = table.data.findIndex((r: ITableData)=>r.id === form.data?.id)
				table.updateRow(index, form.data)
				const data = JSON.stringify(form.data)
				this.webswingInstance?.performAction({actionName:"editListItem",data})
				toast.show('Record has been updated sucessfuly')
			}
		}

		modal.addEventListener('close', modalCloseListener);
	}

	deleteSingleTableRow(data: IFormData) {
		if(!data) return
		const table = this.elementTable;
		const modal = this.elementModalDeleteItem;
		const toast = this.elementToast;

		modal.header = 'Remove Nobel prize winner record';
		modal.show = true;

		const modalCloseListener = (result: CustomEventInit<string>) => {
			modal.removeEventListener('close', modalCloseListener)
			if(result.detail === 'ok') {
				const index = table.data.findIndex((r: ITableData)=>r.name === data.name)
				if (index != null) {
					table.deleteRow(index)
					this.webswingInstance?.performAction({actionName:"removeItem", data: JSON.stringify(data)})
					toast.show('Record has been deleted sucessfuly')
				}
			}
		}
		modal.addEventListener('close', modalCloseListener);
	}

	uiIntro() {
		this.elementIntro.addEventListener('introClick', () => {
			const tabs = this.elementTabs
			const tableTab = this.parent.querySelectorAll('ws-tab')[1];
			tabs.selectTab(tableTab);
		});
	}
}

new MigrationApp(document.body);
